import { useSelector } from "react-redux";
import Blog from "./Blog";

const BlogList = () => {

  const currentBlogs = useSelector( ({ blogs }) => {
    return blogs
  })

  return (
    <div>
      {[...currentBlogs].sort((a, b) => b.likes - a.likes).map(blog => (
        <Blog
          key={blog.id}
          blog={blog}
        />
      ))}
    </div>
  );
};

export default BlogList;
