import { createSlice } from "@reduxjs/toolkit";
import blogService from "../services/blogs"
import { clearNotification, setNotification } from "./notificationReducer";

const blogSlice = createSlice({
  name: "blog",
  initialState:[],
  reducers: {
    increaseLike(state, action) {
      const likeToIncrease = state.find(blog => blog.id === action.payload.id)
      const updatedBlog = {
        ...likeToIncrease,
        likes: likeToIncrease.likes + 1
      }

      blogService.like(updatedBlog)

      return state.map(blog => blog.id !== action.payload.id ? blog : updatedBlog)
    },
    setBlogs (state, action) {
      return action.payload
    },
    appendBlog (state, action) {
      state.push(action.payload)
    },
    removeBlog (state, action) {
      return state.filter(blog => blog.id !== action.payload.id)
    }
  }
})

export const { increaseLike, setBlogs, appendBlog, removeBlog } = blogSlice.actions

export const initializeBlogs = () => {
  return async dispatch => {
    const blogs = await blogService.getAll()
    dispatch(setBlogs(blogs))
  }
}

export const createBlog = (content) => {
  return async dispatch => {
    const newBlog = await blogService.create(content)
    dispatch(appendBlog(newBlog))

    dispatch(setNotification(`a new blog: '${newBlog.title}' was added`));
    setTimeout(() => {
      dispatch(clearNotification())
    }, 3000)
  }
}

export const deleteBlog = (content) => {
  return async dispatch => {
    await blogService.remove(content)
    dispatch(removeBlog(content))

    dispatch(setNotification(`${content.title} was removed`));
    setTimeout(() => {
      dispatch(clearNotification())
    }, 3000)
  }
}

export default blogSlice.reducer