import { useEffect } from "react";
import { Routes, Route, Link } from "react-router-dom"
import Notification from "./components/Notification";
import LoginForm from "./components/LoginForm";
import BlogForm from "./components/BlogForm";
import BlogList from "./components/BlogList";
import UserList from "./components/UserList";
import UserView from "./components/UserView";

import { useDispatch, useSelector } from "react-redux";
import { initializeBlogs, deleteBlog } from "./reducers/blogReducer";
import { initializeUsers } from "./reducers/userReducer";
import { logoutUser } from "./reducers/currentUserReducer";
import { Button, Navbar, Nav } from "react-bootstrap"
import BlogView from "./components/BlogView";



const App = () => {
  // const [blogs, setBlogs] = useState([]);
  // const [message, setMessage] = useState(null);
  const currentUser = useSelector(({ currentUser }) => {
    if (currentUser.length === 0) {
      return null
    } else {
      return currentUser[0]
    }
  })

  const dispatch = useDispatch()

  useEffect(() => {
    dispatch(initializeBlogs());
    dispatch(initializeUsers());
  }, [dispatch]);

  const padding = {
    padding: 5
  }

  const handleLogout = async (event) => {
    event.preventDefault()
    dispatch(logoutUser())
  };


  const handleDelete = async (blogObject) => {
    if (
      window.confirm(
        `Do you really want to remove blog: ${blogObject.title} by ${blogObject.author}`,
      )
    ) {

      dispatch(deleteBlog(blogObject))
    }
  };

  const Home = () => {
    return (
      <div className="container">
        <h2>Blogs App</h2>
        {currentUser ? <BlogForm /> : null}
        <BlogList handleDelete={handleDelete}/>
      </div>
    )
  }

  return (
    <div className="container">
      <Navbar collapseOnSelect expand="lg" bg="dark" variant="dark">
        <Navbar.Toggle aria-controls="responsive-navbar-nav" />
        <Navbar.Collapse id="responsive-navbar-nav">
          <Nav className="me-auto">
            <Nav.Link href="#" as="span">
              { currentUser
                ? <div>
                  {currentUser.name} is currently logged in
                  <Button variant="primary" onClick={handleLogout}>logout</Button>
                </div>
                : <Link style={padding} to="/login">Login</Link>
              }
            </Nav.Link>
            <Nav.Link href="#" as="span">
              <Link style={padding} to={"/"}>Home</Link>
            </Nav.Link>
            <Nav.Link href="#" as="span">
              <Link style={padding} to={"/users"}>Users</Link>
            </Nav.Link>
          </Nav>
        </Navbar.Collapse>
      </Navbar>
      <Notification />

      <Routes>
        <Route path="/" element={<Home />} />
        <Route path="/login" element={currentUser ? <Home /> : <LoginForm />} />
        <Route path="/users" element={<UserList />} />
        <Route path="/users/:id" element={<UserView />} />
        <Route path="/blogs/:id" element={<BlogView />} />
      </Routes>
    </div>
  );
};

export default App;
