import { useSelector } from "react-redux"

const Notification = () => {
  const notification = useSelector(({ notification }) => {
    if (notification.value !== "")
      return (
        <div>
          {notification.value}
        </div>
      )
    else {
      return null
    }
  })

  const style = {
    color: "green",
    border: "solid",
    padding: 10,
    borderWidth: 1,
  }

  return (
    <div>
      {notification && (
        <div style={style}>
          {notification}
        </div>
      )}
    </div>
  )
}

export default Notification;
